    FcbImporterService.prototype._convertInputToLines = function (input) {
        if (!input) {
            return [];
        }
        return input.split(/[\n]/);
    };
    FcbImporterService.prototype._convertFCBankLineDataToTransaction = function (line, startDate, endDate) {
        var transaction = {};
        var parts = line.split("\t");
        transaction.transaction_date = new Date(parts[0]);
        transaction.description = parts[1];
        var withdrawalAsStringWithDollarSign = parts[2];
        var depositAsStringWithDollarSign = parts[3];
        transaction.withdrawal_amount = this.extractNumberFromDollarString(withdrawalAsStringWithDollarSign);
        transaction.deposit_amount = this.extractNumberFromDollarString(depositAsStringWithDollarSign);
        var start = new Date(startDate);
        var end = new Date(endDate);
        // confirm that the transaction date is within the period
        if (transaction.transaction_date < start ||
            transaction.transaction_date > end) {
            transaction.deleted = true;
        }
        // also remove any transactions with 0 amounts
        if (transaction.withdrawal_amount === 0 &&
            transaction.deposit_amount === 0) {
            transaction.deleted = true;
        }
        return transaction;
    };
